export default {
	data() {
		return {
			status: 0,
			id: '',
			list:'',
			style:'backgorund:#A8B5C6;',
		};
	},
	components: {},
	computed: {},
	methods: {},
	onLoad(e) {
		let data = {
			id: e.id
		}
		this.$store.dispatch('getProfitDetail', data).then(res=>{
			this.list = res.data
		})
	}
}
